import { useEffect, useState } from 'react';
import { ProductsCardType } from '../../helpers/types/ProductsCardType';
import { Product } from '../../helpers/types/Product';
import { getProducts } from '../../api/getProducts';
import { ProductCard } from '../ProductCard/ProductCard';
import './ProductsSlider.scss';

type Props = {
  type: ProductsCardType
};

export const ProductsSlider: React.FC<Props> = ({ type }) => {
  const [products, setProducts] = useState<Product[]>([]);

  useEffect(() => {
    getProducts()
      .then((phones) => setProducts(phones.slice(3, 5).reverse()));
  }, []);

  return (
    <section className="section">
      <h2 className="section__title">{type}</h2>
      <div className="section__productCard-slides">
        {products.map((product) => (
          <ProductCard product={product} type={type} key={product.phoneId} />
        ))}
      </div>
    </section>
  );
};
